h1 {
  color: blue;
  background-color: #d3d3d3;
  border-radius: 1px;
  border: solid;
  border-color: darkgrey;
}

.paragraph {
  color: rgb(0, 255, 0);
  background-color: rgba(255, 255, 0, 0.5);
}

div p {
  color: red;
  font-size: 18px;
  font-weight: bold;
}

* {
  text-align: center;
  font-family: Arial, sans-serif;
}

div span {
  color: #a020f0;
  font-style: italic;
}
